import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import { InfmanagementComponent } from './infmanagement.component';
import {InfmanagementChildModule} from './infmanagement-child/infmanagement-child.module';

const routes: Routes = [
    {
      path: '',
      component: InfmanagementComponent,
      children: [
        {
          path: '',
          loadChildren: './infmanagement-child/infmanagement-child.module#InfmanagementChildModule'
        }
      ]
    }
  ];




@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
  })
  export class InfmanagementRoutingModule {}